.team__item {
    height: 380px;
    overflow: hidden;
    border-radius: 1rem;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    padding: 2rem;
    text-align: center;

    &--text {
        transition: all 0.5s;
    }
    
    p {
        padding-bottom: 0.5rem;
    }
    
    &::after {
        content: "";
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.2);
        position: absolute;
        top: 0;
        left: 0;
        z-index: -1;
    }
    
    &--image {
        transition: all 0.5s;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: -2;
        transition: all 0.5s;

        span,
        img {
            transition: all 0.5s;
            display: block;
            width: 100%;
            height: 100%;
        }
    }

    &:hover {
        .team__item {
            &--text {
                transform: scale(1.2);
            }
        }
    }
}

// team end