.difference {
    display: flex;
    justify-content: center;
    gap: 1rem;

    &__box {
        padding: 2rem;
        background-color: #242424;
        border-radius: 1rem;
        width: 40%;

        &--title {
            text-align: center;
            padding-bottom: 3rem;
        }

        &--item {
            display: flex;
            padding: 1rem 0;
            gap: 1rem;
            border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        }
    }
}

// difference end