.pricing {
    display: flex;
    justify-content: center;
    gap: 1rem;
    flex-wrap: wrap;

    &__box {
        width: 40%;
        padding: 1.5rem 1rem;
        background-color: #fff;
        border-radius: 1rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        &--title {
            text-align: center;
            padding-bottom: 2rem;

            p {
                font-family: sans-serif !important;
            }
        }

        &--item {
            display: flex;
            gap: 1rem;
            padding: 1rem 3rem;
        }

        &--btns {
            display: flex;
            justify-content: center;
        }
    }
}