.partners__talk {
    &.second {
        .swiper-wrapper {
            padding: 2rem 0 6rem 0;
        }

        .swiper-slide-next {
            .partners__talk {
                &--item {
                    transition: all .5s;
                    transform: scale(1.2, 1.2);
                }
            }
        }

        .partners__talk--item {
            transition: all .5s;
            background-color:#fff;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            padding: 3rem 2rem;
            border-radius: 1rem;
            position: relative;
            overflow: hidden;
            z-index: 0;
            cursor: pointer;

            &::after {
                content: "";
                width: 100%;
                height: 100%;
                background-color:rgb(214, 253, 60);
                position: absolute;
                top: 0;
                left: -300%;
                z-index: -1;
                transform: rotate(45deg) scale(2);
                transition: all 0.5s;
            }

            &:hover {
                &::after {
                    left: 0;
                }

                p {
                    color:#fff;
                }
            }


            p {
                padding-bottom: 1rem;
            }
        }

        .partners__talk--image {
            width: 80px;
            height: 80px;
            border-radius: 50%;
            margin-bottom: 1rem;
        }

        .partners__talk--left {
            width: 100%;
        }
    }
}

