a{
    text-decoration: none;
}
ul , ol {
    list-style: none;
}
button{
    cursor:pointer;
}
img{
    max-width:100%;
}

input{
    padding: 1.5rem ;
    border-radius: 0.5rem;
    border:none;
    background-color: rgba(255 , 255 , 255 , 0.1);
    width:100%;
    margin-bottom: 2rem;
    color:white;
    &:focus{
        outline: none;
    }
}