// swiper paginations
.partners__talk {
    .swiper-pagination-bullet {
        width: 1rem;
        height: 1rem;
        background-color:#A0A7BA;
        opacity: 1;
        transition: all 0.5s;
    }

    .swiper-pagination-bullet-active {
        transition: all 0.5s;
        width: 5rem;
        border-radius: 0.5rem;
        background-color:rgb(214, 253, 60);
    }
}

// end pagination