.parts{
    // border-top:2px dashed ${Styles?.colors?.grey};
    // border-bottom:2px dashed ${Styles?.colors?.grey};
    margin:5rem 0 2rem 0;
    .container{
      padding:0;
      position:relative;
      &::after{
        content:"";
        width:-1px;
        height:99.9%;
        // border:2px dashed ${Styles?.colors?.grey};
        position:absolute;
        top:0;
        left:50%;
        transform:translateX(-50%);
      }
    }
    &__content{
      display:flex;
      flex-direction:column;
      &--box{
        display:flex;
        align-items:center;
        position:relative;
        min-height:400px;
        overflow:hidden;
        p{
          padding-bottom:1rem;
        }
        &:nth-child(even){
          flex-direction:row-reverse;
          .parts__content--img{
            left:0;
            border-radius: 2rem;
          }
          .parts__content--text{
            padding: 0 10rem 0 2rem;
            &::after{
              right:8%;
              left:auto;
            }
          }
        } 
      }
      &--text{
        width:50%;
        padding: 0 2rem 0 10rem;
        &::after{
          content:"";
          width:-1px;
          height:100%;
          // border:2px dashed ${Styles?.colors?.grey};
          position:absolute;
          top:0;
          left:8%;
          transform:translateX(-50%);
        }
      }
      &--img{
        position:absolute;
        top:0;
        right:0;
        width:50%;
        height:100%;
        overflow:hidden;
        background-color:rgba(255, 255, 255, 0.4);
        border-radius:2rem;
        border:7px solid rgb(214, 253, 60);
        iframe , div{
          width: 100% !important;
          height: 100% !important;
        }
        span{
          width:100%;
          height:100%;
          display:block;
          img{
            width:100%;
            height:100%;
          }
        }
      }
      &--btns{
        @media screen and (max-width:768px){
          display: none;
        }
      }
      &--btnsMedia{
        display: none;
        @media screen and (max-width:768px){
          display: flex;
          justify-content: center;
          align-items: center;
          padding: 2rem 0;
          width: 100%;
          a{
            width: 90%;
          }
          button{
            width: 100%;
            margin: 0 auto;
            background-color: rgb(194, 236, 26);
            border: none;
            color: black;
            font-weight: bold;
          }
        }
      }
    }
  } // parts end