@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700;900&display=swap');

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: 'Raleway', sans-serif;
    scroll-behavior: smooth;
}

// variables
$black: #000;
$white: #fff;
$blue: rgb(100, 121, 18);
$grey: #A0A7BA;
$lightBlue: rgb(214, 253, 58);
$bg: linear-gradient(270deg, rgba(0, 0, 0, 0.7) 0%,
        rgba(0, 0, 0, 0) 44.72%, rgba(0, 0, 0, 0) 100%);

html , body {
    background-color: $black;
    overflow-x: hidden;
    position: relative;
    z-index: 0;
}

::selection {
    color: $white;
    background: $blue;
}

/* width */
::-webkit-scrollbar {
    width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
    background: $blue;
}

/* Handle */
::-webkit-scrollbar-thumb {
    background:$lightBlue;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: $black;
}

.container {
    max-width: 1330px;
    margin: 0 auto;
    padding: 0 10px;
}

