.portfolio {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    gap: 2rem;
    flex-wrap: wrap;
    padding: 5rem 0 2rem 0;

    &__box {
        width: 48%;
        border-radius: 2rem;
        overflow: hidden;
        height: 500px;
        background-size: cover;
        // background-position: center;
        padding: 2rem 1rem;
        position: relative;
        z-index: 0;
        transition: all 1s;
        cursor: pointer;

        &::after {
            content: "";
            width: 100%;
            height: 100%;
            top: 0%;
            left: 0%;
            background: rgba(0, 0, 0, 0.4);
            position: absolute;
            z-index: -1;
        }

        &--image {
            position: absolute;
            bottom: -20%;
            left: -20%;
            transform: rotate(-15deg);
            transition: all 1s;
        }

        p {
            transition: all 0.5s;
        }

        &:hover {
            background-position: left;

            p {
                font-size: 2rem;
            }

            .portfolio__box--image {
                transform: rotate(10deg) scale(1.3, 1.2);
                left: 20%;
                bottom: -10%;
            }
        }

        &.big {
            height: 700px;
            margin-top: -100px;

            .portfolio__box--image {
                transform: scale(1.3, 1.2);
                left: 20%;
                bottom: -10%;
            }

            &:hover {
                .portfolio__box--image {
                    transform: scale(1.5, 1.5);
                    left: 20%;
                    bottom: 0%;
                }
            }
        }

    }
}

// portfolio end