.services__content {
    background-color:#fff;
    padding: 1rem 1rem 2rem 1rem;
    border-radius: 1rem;
}

.services {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    &__box {
        width: 19%;
        text-align: center;

        p {
            padding-bottom: 1rem;
        }

        &--icon {
            margin: 0 auto 1rem auto;
        }
    }
}

