main{
    position:relative;
    z-index:0;
    
    &::after{
        content:"";
        background-image: url(../assets/images/bg.jpg);
        background-size: cover;
        background-position: center;
        position:absolute;
        top:0;
        left:0%;
        width:100%;
        height:100%;
        z-index:-2;
        background-attachment:fixed;
    }
    &::before{
      content:"";
      position:absolute;
      top:0;
      left:0;
      width:100%;
      height:100%;
      z-index:-1;
      background:radial-gradient(50% 50% at 50% 50%, rgba(0, 0, 0, 0.2) 0%, #000000 100%);
    }

    .main__content{
      width:70%;
      text-align:center;
      margin:0 auto;
      padding: 3rem 0;


      p{
        padding-bottom:0.6rem;
      }
      .main__shape{
        transform:translate(-22% , -18%);
      }
      .main__play{
        width:4rem;
        height:4rem;
        margin:0 auto;
        background-color:rgba(255, 255, 255, 0.4);
        display:flex;
        justify-content:center;
        align-items:center;
        border-radius:50%;
        padding-left:0.3rem;
        transition:all 0.5s;
        position:absolute;
        left:40%;
        transform:translate(0 , -50%);

        &:hover{
          background-color:#fff;
          cursor:pointer;
          svg{
            path{
              fill:rgb(100, 121, 18);
            }
          }
        }
      }
    }
  } // main end