.offcanvas {
    width: 98%;
    height: 98vh;
    position: fixed;
    top: -200%;
    left: 1%;
    background-color: rgba(255, 255, 255, 0.6);
    backdrop-filter: blur(5px);
    z-index: 999;
    border-radius: 1rem;
    overflow-y: scroll;
    transition: all 0.5s;

    &::-webkit-scrollbar {
        display: none;
    }

    &__close {
        position: absolute;
        top: 1rem;
        right: -100%;
        transition: all 1s;
        cursor: pointer;
    }

    &__box {
        display: flex;
        flex-wrap: wrap;
        padding: 2rem 0;

        &--left {
            width: 30%;
            @media screen and (max-width:768px){
                width: 100%;
            }
        }
        
        &--right {
            width: 70%;
            @media screen and (max-width:768px){
                width: 100%;
            }
        }
    }

    &__title{
        padding-right: 2rem;
    }

    &__content {
        width: 80%;
        text-align: center;
        margin: 0 auto;
        padding: 1rem;

        a {
            color: black;
        }

        ul {
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;

            li {
                width: 100%;
                a {
                    width: 100%;
                    font-size: 1.2rem;
                    text-align: start;
                    padding: 1rem 1rem 1rem 3rem;
                    font-weight: bold;
                    border: 1px solid transparent;
                    border-radius: 1rem;
                    display: flex;
                    justify-content: space-between;
                    svg{
                        transition: all 0.5s;
                    }

                    &:hover {
                        svg{
                            transform: rotate(-20deg);
                        }
                        border-color: black;
                    }
                }
            }
        }
    }

    .socials,
    .address__item {
        justify-content: flex-start;
        text-align: start;

        svg {
            path {
                fill: #000;
            }
        }
    }

    .socials {
        .socials__item {
            background: grey;

            svg {
                path {
                    fill: #fff;
                }
            }
        }
    }

    &.active {
        top: 1%;

        .offcanvas__close {
            right: 1rem;
        }
    }


    @media screen and (max-width: 560px) {
        &__content {
            width: 100%;
        }
    }

}