footer {
    border-top: 3px dashed grey;

    .footer__content {
        display: flex;
        justify-content: space-between;
        padding: 3rem 0;

        &--left {
            width: 30%;
        }

        &--right {
            width: 30%;

        }

        &--center {
            ul {
                li {
                    padding-top: 0.5rem;
                }
            }
        }

        a {
            color: grey;
        }

        @media screen and (max-width:650px) {
            flex-wrap: wrap;
            gap: 2rem;

            &--left,
            &--right,
            &--center {
                width: 100%;
            }
        }
    }
}

.address {
    &__item {
        display: flex;
        gap: 1rem;
        padding-top: 1rem;
    }
}

.socials {
    display: flex;
    gap: 1rem;
    padding: 1rem 0;

    &__item {
        width: 3rem;
        height: 3rem;
        background: #fff;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        transition: all 0.5s;

        &:hover {
            background-color: #0035C7;
        }
    }
}

.footer__bottom {
    padding: 1rem 0;
    border-top: 1px solid grey;

    &--content {
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1rem;
        flex-wrap: wrap;
    }

    &--right {
        display: flex;
        gap: 2rem;
    }

    @media screen and (max-width:650px) {
        &--content {
            justify-content: center;
        }
    }
}