.modal{
    width: 100%;
    height: 100vh;
    background-color: rgba(255 , 255 , 255 , 0.4);
    backdrop-filter: blur(5px);
    position: fixed;
    top: 0;
    left: 0;
    z-index:99;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s;
    opacity: 0;
    visibility: hidden;
    &__box{
        width: 40%;
        background-color: #fff;
        position: absolute;
        padding: 2rem 2rem 4rem 2rem;
        text-align: center;
        border-radius: 1rem;
        box-shadow: 1px 10px 10px rgba(0,0,0,0.2);
        transform: scale(0);
        transition: all 0.5s;
        @media screen and (max-width:560px){
            width:90%;
        }
    }
    &__header{
        display: flex;
        justify-content: flex-end;
    }
    &.active{
        opacity: 1;
        visibility: visible;
        .modal__box{
            transform: scale(1);
        }
    }
}