.faq {

    // overflow:hidden;
    &__content {
        &:first-child {
            border-radius: 0.5rem 0.5rem 0 0;
        }

        &:last-child {
            border-radius: 0 0 0.5rem 0.5rem;
        }

        background-color:#fff;
    }

    &__header {
        background-color:#fff;
        padding: 2rem;
        border-bottom:1px solid rgb(100, 121, 18);
        display: flex;
        justify-content: space-between;
        align-items: center;
        gap: 1rem;
        cursor: pointer;
        transition: all 0.5s;

        &:hover {
            transform: scale(1.01);
        }

        &.active {
            .faq__shape {
                transition: all 0.5s;
                transform: rotate(180deg);
            }
        }
    }

    &__body {
        padding: 2rem;
        height: 0;
        position: absolute;
        visibility: hidden;
        opacity: 0;
        transition: all 0.5s;

        &.active {
            transition: all 0.5s;
            height: auto;
            position: static;
            visibility: visible;
            opacity: 1;
        }
    }
}

// faq end