.partners__talk {
    .swiper-wrapper {
        padding-bottom: 5rem;
    }

    &--box {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        gap: 0.5rem;
        position: relative;
        z-index: 0;
        padding: 2rem 1rem;

        &::after {
            content: "";
            width: 40%;
            height: 100%;
            background:url(../assets/images/partner__talk.jpg);
            position: absolute;
            top: 0;
            left: 0;
            z-index: -1;
            background-size: cover;
            background-position: center;
            border-radius: 1.25rem;
        }
    }

    &--left {
        width: 25%;
    }

    &--body {
        width: 40%;
        padding-left: 1rem;
    }

    &--image {
        width: 180px;
        height: 180px;
        overflow: hidden;
        background-color:#A0A7BA;
        border-radius: 1.25rem;

        span {
            width: 100%;
            height: 100%;
            display: block;

            img {
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
    }
}

// partners talk end